
import { defineComponent } from 'vue';
import { store } from '../../../store/store';
import TheShareIcon from '@/assets/svg/TheShareIcon.vue';
import TheFriendUsername from '@/components/userprofile/TheFriendUsername.vue';
import swal from 'sweetalert2';
import { supabase } from '../../lib/supabaseClient';
import AddBtn from '../../buttons/TheAddBtn.vue';
import CloseModalButton from '../../buttons/TheCloseModalBtn.vue';
import LoadingButton from '../../buttons/TheLoadingButton.vue';

export default defineComponent({
  name: 'TheLinkEdit.vue',
  components: {
    TheShareIcon,
    TheFriendUsername,
    AddBtn,
    CloseModalButton,
    LoadingButton,
  },

  props: {
    item: Object,
  },

  data() {
    return {
      nBtn: true,
      loading: false,
      key: 1,
    };
  },
  methods: {
    async shareChatBtn() {
      const item = this.item as any;

      this.nBtn = false;
      this.loading = true;
      const username = store.friendUsername;
      const email = username.toLowerCase() + '@linky.com';

      const data = {
        username: username,
        email: email,
        linkname: item.linkname,
        linkdescription: item.linkdescription,
        category: item.category,
        link: item.link,
        chatRoom: item.chatRoom,
        chatKey: item.chatKey,
        iv: item.iv,
      };

      try {
        await supabase
          .from('link')
          .insert(data)
          .then(() => {
            swal
              .fire({
                icon: 'success',
                text: `You´ve successfully shared the chat ${data.linkname}.`,
                timer: 1500,
                showConfirmButton: false,
              })
              .then(() => {
                this.executeCleanUp();
                store.retieveAllLinks();
              });
          });
      } catch (error) {
        this.executeCleanUp();
        console.error('shareChatBtn Error: ' + error);
      }
    },

    executeCleanUp() {
      this.key = this.key + 1;
      this.nBtn = true;
      this.loading = false;
    },
  },
});
